import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
import toImg from "../../../components/toImg.vue"
export default {
	data() {
		return {
			items: ['最新','热门'],
			current: 0,
			id:0,
			params:{
				limit:18,
				skip:0,
				order:"new"
			},
			vertical:[],
			hasmore:true
		}
	},
	methods: {
		getvertical() {
			uni.showLoading({
				title:"加载中"
			})
			uni.request({



				

				url:"http://157.122.54.189:9088/image/v1/vertical/category/" + this.id + "/vertical",

				data:this.params,
				success: (result) => {
					//console.log(result);
					if( result.data.res.vertical.length == 0 ){
						uni.showToast({
							title:"数据没有了",
							icon:"none"
						})
						this.hasmore = false;
						return;
					}
					this.vertical = [...this.vertical,...result.data.res.vertical];
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		onClickItem(e) {
			
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
			if(this.current == 0){
				this.params.order = 'new';
				this.params.skip = 0;
				this.vertical =[];
				
			}else{
				this.params.order = 'hot';
				this.params.skip = 0;
				this.vertical =[];
									
			}
			
			
			this.getvertical();
		},
		scrolltolower() {
			if(this.hasmore) {
				this.params.skip += this.params.limit;
				this.getvertical();				
			}else{
				uni.showToast({
					title:"数据没有了",
					icon:"none"						
				})
			}

		}
	},
	components: {
		uniSegmentedControl,
		toImg
	},
	onLoad(options) {
		this.id = options.id;
		this.getvertical();
	}
}
